.Nav{

   background: black;
   position: relative;
   padding:0.7rem 0.5rem;
  
}
.Nav .NavLinks{
    display: flex;
    align-items: center;
}
.NavLinks a{
    color:wheat;
    font-weight: bold;
    padding:0.5rem;
}
.NavLinks .logo{
    padding:0;
    padding-right: 0.5rem;
   
}

.Nav .logo{
    width: 100px;
    height: 60px;

}
.Nav .burger{
    position: absolute;
    right: 20px;
    top:50%;
    transform:translateY(-50%);
    display: none;
}
.Nav .burger i{
    color: wheat;
  
}

@media(max-width:768px){
    .Nav .burger{
        display:block;
    }
}